import React from 'react'
import css from './css.module.scss'

export default function ContactForm() {
  return (
    <div class="container" id="contact-us">
      <div class="jumbotron">
        <h2 className={css.title}>Get in touch!!!</h2>
        <hr />
        <div class="container">
          <form
            id="contact-form"
            method="post"
            action="./contact-form-handler.php"
          >
            <div class="form-row">
              <div class="form-group col-md-6">
                <label htmlFor="name">Name</label>
                <input
                  type="name"
                  name="name"
                  class="form-control"
                  id="name"
                  placeholder="Name"
                />
              </div>
              <div class="form-group col-md-6">
                <label htmlFor="email">Email</label>
                <input
                  type="email"
                  name="email"
                  class="form-control"
                  id="email"
                  placeholder="Email"
                />
              </div>
            </div>

            <div class="form-row">
              <div class="form-group col-md-6">
                <label htmlFor="number">Phone Number</label>
                <input
                  type="number"
                  class="form-control"
                  id="number"
                  name="phone"
                  placeholder="Phone Number"
                />
              </div>
              <div class="form-group col-md-6">
                <label htmlFor="Deal">Deal</label>
                <select id="deal" name="deal" class="form-control">
                  <option>Small Company</option>
                  <option>Medium Company</option>
                  <option>Student Pack</option>
                </select>
              </div>
            </div>

            <div class="form-row">
              <div class="form-group col-md-6">
                <label for="sel1">Project</label>
                <select
                  multiple
                  id="project"
                  name="project"
                  class="form-control"
                >
                  <option>ReactJS</option>
                  <option>VueJS</option>
                  <option>Flask </option>
                  <option>Website (HTML/CSS)</option>
                  <option>Other</option>
                </select>
                <small id="project" class="form-text text-muted">
                  * Hold shift to select multiple items.
                </small>
              </div>
            </div>

            <div class="form-group col-md-12">
              <label htmlFor="message">Message</label>
              <textarea
                class="form-control"
                rows="5"
                placeholder="Message"
                id="message"
                name="message"
              />
            </div>
            <div class="form-group col-md-6">
              <button type="submit" class="btn btn-primary">
                Submit
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}
