import React from 'react'

export default function About() {
  return (
    <>
      <div class="container" id="about">
        <div class="jumbotron">
          <h2 class="text-center">Hosting is hard!!!</h2>
          <hr />
          <h4 align=" justify">
            One of the easiest skills to learn these days is making websites or
            web applications. But soon, you realise that, somehow, you need to
            stick those HTML and CSS files onto the internet. It's one thing to
            learn to make a website, but hosting it is extremely harder. Getting
            a domain name, finding a server/hosting provider, linking domains to
            IPs...... You get the idea. But, this can be simplified for you. I
            spend months learning how to host, you don't have to. Just develop
            the websites and webApps, we will host for you easily and fast!
          </h4>
        </div>
      </div>
      <br />
      <div class="container">
        <div class="jumbotron">
          <h2 class="text-center">What we can help you with</h2>
          <hr />
          <h4 align=" justify">
            We will host your apps on our server for a small fee. We will do
            server configurations for your app or website. All backend and
            frontend app included, we will set up databases if needs be. You
            will chose your domain name and your website or app will be hosted
            under:<code>"your-domain-name".enum.co.za</code>. The small fee will
            include server management and maintence, and you can do your
            releases/deployments as often as you need to. You will have 24 hour
            technical assistance on call/messaging.
          </h4>
        </div>
      </div>
    </>
  )
}
